<template>
  <div>
    <swiper :options="swiperOption" class="swiper-wrap"  ref="mySwiper" v-if="banner.length!=0">
      <swiper-slide v-for="(item, index) in banner" :key="index" >
        <div class="banner-info">
          <p class="bantxt">{{item.title}}</p>
          <div class="line"> </div>
          <p>{{item.text}}</p>
        </div>
        <!-- <img :src="item.image" alt="" /> -->
      </swiper-slide>
      <!-- 常见的小圆点 -->
      <div class="swiper-pagination"  v-for="(item, index) in banner" :key="index" slot="pagination" ></div>
    </swiper>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'bannerSwiper',
  components: {
    swiper,
    swiperSlide
  },
  data () {
    const that = this
    return {
      banner: [
        {
          title: '专注，专业，高效',
          text: '格林物流是格林集团旗下物流事业部，2009年在无锡成立了格林物流无锡有限公司。格林物流拥有优质的铁运、汽运、水运、配送等运输资源，是一家集公路运输、铁路运输、内贸集装箱运输、站内存储、仓储管理、分销配送和第四方物流服务于一体的第三方物流企业，主营业务有运输服务、货代服务、仓储与配送服务、物流整体方案设计、物流信息服务。'
        },
        {
          title: '专注，专业，高效',
          text: '格林物流是格林集团旗下物流事业部，2009年在无锡成立了格林物流无锡有限公司。格林物流拥有优质的铁运、汽运、水运、配送等运输资源，是一家集公路运输、铁路运输、内贸集装箱运输、站内存储、仓储管理、分销配送和第四方物流服务于一体的第三方物流企业，主营业务有运输服务、货代服务、仓储与配送服务、物流整体方案设计、物流信息服务。'
        },
        {
          title: '专注，专业，高效',
          text: '格林物流是格林集团旗下物流事业部，2009年在无锡成立了格林物流无锡有限公司。格林物流拥有优质的铁运、汽运、水运、配送等运输资源，是一家集公路运输、铁路运输、内贸集装箱运输、站内存储、仓储管理、分销配送和第四方物流服务于一体的第三方物流企业，主营业务有运输服务、货代服务、仓储与配送服务、物流整体方案设计、物流信息服务。'
        }
      ],
      imgIndex: 1,
      swiperOption: {
        //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        notNextTick: true,
        //循环
        loop: true,
        //设定初始化时slide的索引
        initialSlide: 0,
        //自动播放
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          /* 触摸滑动后是否继续轮播 */
          disableOnInteraction: false
        },
        //滑动速度
        speed: 800,
        //滑动方向
        direction: "horizontal",
        //小手掌抓取滑动
        grabCursor: true,
        on: {
          //滑动之后回调函数
          slideChangeTransitionStart: function() {
            /* realIndex为滚动到当前的slide索引值 */
            that.imgIndex= this.realIndex - 1
          },
        },
        //分页器设置
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          type: "bullets"
        }
      }
    }
  },
  created() { 
  }
}
</script>

<style scoped>

</style>



